import React, { useState } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import './css/AddModal.css'
import {
  Form,
  Container,
  Row,
  Col,
} from "react-bootstrap"
import {
  Modal,
  ButtonToolbar,
  Button,
  DatePicker,
  SelectPicker,
  CheckPicker,
  InputNumber,
  Input,
} from 'rsuite'
import { account, way, tagOptions, sortE, sortI } from './data'
import Add from '../icon/add.png'
import selectExpenses from '../icon/expenses.png'
import selectIncome from '../icon/income.png'
import Unselected from '../icon/unselected.png'

function ModalLabel (params) {
  const [income, setIncome] = useState(false)
  const [expenses, setExpenses] = useState(true)
  const [type, setType] = useState(2)

  const clickIncome = (e, msg) => {
    e.preventDefault()
    setExpenses(false)
    setIncome(true)
    setType(1)
    // console.log('', msg, e)
  }
  const clickExpenses = (e, msg) => {
    e.preventDefault()
    setExpenses(true)
    setIncome(false)
    setType(2)
    // console.log('', msg, e)
  }

  return (
    <>
      <Container>
        <Row>
          <Col xs={2} className='modal-labelHome'>
            <img
              type='button'
              onClick={(e) => clickIncome(e, 'income')}
              src={income ? selectIncome : Unselected}
              className='record-ieItem'
              alt={income ? 'selectIncome' : 'Unselected'}
              title={income ? 'Select income' : 'Unselect income'}
              width={'18px'} />
          </Col>
          <Col xs={2} className='modal-labelHome'>
            <img
              type='button'
              onClick={(e) => clickExpenses(e, 'expenses')}
              src={expenses ? selectExpenses : Unselected}
              className='record-ieItem'
              alt={expenses ? 'selectExpenses' : 'Unselected'}
              title={expenses ? 'Select expenses' : 'Unselect expenses'}
              width={'18px'} />
          </Col>
        </Row>
      </Container>
      <Form target='_top'>
        <Modal.Body className='modalBody'>
          <Container>
            <Row className='modalRow'>
              <Col xs={6}>
                <span className='modalBody-item'></span>
                <DatePicker oneTap defaultValue={new Date()} className='modalSelect' />
              </Col>
              <Col xs={6}>
                <span className='modalBody-item'></span>
                {ChangeSort(type)}
              </Col>
            </Row>
            <Row className='modalRow'>
              <ChangeWay />
            </Row>
            <TagSelect />
            <Row className='modalRow'>
              <Col xs={12}>
                <span className='modalBody-item'></span>
                <Input as='textarea' placeholder="" />
              </Col>
            </Row>
            <Row className='confirmBtnRow'>
              <Button
                type='submit'
                className='confirmBtn'
                variant="primary"
              >
                confirm
              </Button>
            </Row>
          </Container>
        </Modal.Body >
      </Form>

    </>
  )
}


//
const ChangeSort = (type) => {
  let sort = []
  let sortT = []
  if (type === 1) {
    const dataI = sortI.map(
      item => ({
        label: item.label,
        value: item.value,
        role: item.transfer ? '' : ''
      })
    )
    return (
      <SelectPicker data={dataI} placeholder="" groupBy="role" className='modalSelect' />
    )
  }
  else if (type === 2) {
    for (let index = 0; index < sortE.length; index++) {
      if (sortE[index].transfer === false) {
        sort.push({ value: sortE[index].value, label: sortE[index].label })
      } else {
        sortT.push({ value: sortE[index].value, label: sortE[index].label })
      }
    }
    const dataE = sortE.map(
      item => ({
        label: item.label,
        value: item.value,
        role: item.transfer ? '' : ''
      })
    )
    return (
      <SelectPicker data={dataE} placeholder="" groupBy="role" className='modalSelect' />
    )
  }
}

//
function ChangeWay (params) {
  const [acc, setAcc] = useState(1)
  let accIndex = 0
  let accWay = []
  for (let index = 0; index < account.length; index++) {
    if (account[index].value === acc) {
      accIndex = index
    }
  }
  console.log(accIndex)
  for (let index = 0; index < way.length; index++) {
    if (account[accIndex].way1 === way[index].value) {
      accWay.push({ value: way[index].value, label: way[index].label })
    }
    if (account[accIndex].way2 === way[index].value) {
      accWay.push({ value: way[index].value, label: way[index].label })
    }
  }
  const AccData = account.map(
    item => ({
      label: item.label,
      value: item.value
    })
  )
  console.log(acc)
  return (
    <>
      <Col xs={6}>
        <span className='modalBody-item'></span>
        <SelectPicker
          data={AccData}
          defaultValue={"1"}
          className='modalSelect'
          onChange={setAcc}
        />
      </Col>
      <Col xs={6}><span className='modalBody-item'></span>
        <SelectPicker data={accWay} placeholder="" className='modalSelect' />

      </Col>
    </>
  )
}

function TagSelect (params) {
  return (
    <>
      <Row className='modalRow'>
        <Col xs={6}>
          <span className='modalBody-item'></span>
          <InputNumber placeholder="" step={100} />
        </Col>
        <Col xs={6}>
          <span className='modalBody-item'></span>
          <CheckPicker
            name='tag'
            sticky
            data={tagOptions}
            placeholder=""
            className='modalSelect'
          />
        </Col>
      </Row>
    </>
  )
}




const AddBtn = () => {
  const [open, setOpen] = React.useState(false)
  const handleOpen = () => setOpen(true)
  const handleClose = () => setOpen(false)
  return (
    <>
      <ButtonToolbar>
        <Button onClick={handleOpen} className='CircleBtn addBtn'>
          <img src={Add} alt='Add' title='Add' width={'15px'} />
        </Button>
      </ButtonToolbar>
      <Modal
        open={open}
        onClose={handleClose}
        backdrop={'static'}
        overflow={false}
      >
        <Modal.Header>
        </Modal.Header>
        <Modal.Body>
          <ModalLabel />
        </Modal.Body>
      </Modal>
    </>
  )
}



export { AddBtn }